.CardBlock {
    margin-bottom: 145px !important;
    .cardBlock-inner {
        margin-bottom: 40px;
        .cardBlock-inner-title {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
            color: #151515;
        }
    }
    .cardBlock-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

@media screen and (max-width: 960px) {
    .CardBlock {
        margin-bottom: 100px !important;
    }
}

@media screen and (max-width: 768px) {
    .CardBlock {
        .Card {
            // margin-right: 20px;
        }
    }
}

@media screen and (max-width: 375px) {
    .CardBlock {

        .cardBlock-bottom {
            flex-wrap: wrap;
            align-items: flex-start;
            .Card {
                margin-bottom: 20px;
            }
        }
        
    }
}