.Carousel {
	border-top: 1px solid #d1d1d1;
	margin-top: 75px;
	margin-bottom: 90px;

	.slick-prev {
		position: relative;
		left: -9px;
		top: 175px;
		z-index: 1;
		width: 50px;
		height: 50px;
	}

	.slick-next {
		position: relative;
		right: -1370px;
		top: -103px;
		z-index: 1;
		width: 50px;
		height: 50px;
	}

	.carousel-inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 70px;
		margin-bottom: 45px;

		.carousel-btn {
			font-style: normal;
			font-weight: bold;
			font-size: 15px;
			line-height: 22px;
			color: #151515;
			outline: none;
			border: none;
			background-color: transparent;
			.carousel-arrow {
				margin-left: 15px;
			}
		}

		.cardBlock-inner-title {
			font-family: "Poppins";
			font-style: normal;
			font-weight: 600;
			font-size: 18px;
			line-height: 27px;
			color: #151515;
		}
	}

	.carousel-cards {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

@media screen and (max-width: 960px) {
	.Carousel {
		position: relative;

		.slick-prev {
			position: absolute;
			left: 5px;
			top: 145px;
		}

		.slick-next {
			position: absolute;
			right: 5px;
			top: 145px;
			bottom: 0;
		}
	}
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 375px) {
	.Carousel {
		.slick-prev {
			left: 15px;
			top: 100px;
			width: 27px;
		}

		.slick-next {
			right: 15px;
			top: 100px;
			width: 27px;
		}
	}
}
